.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.7)),
    url("https://images.unsplash.com/photo-1618773928121-c32242e63f39?ixlib=rb-4.0.3&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80");
  min-height: calc(100vh - 65px);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom;
  display: flex;
  align-items: center;
}

.hero-text-block {
  width: 90%;
  margin: 0 auto;
  color: white;
}

.house-emoji {
  font-size: 2rem;
  margin-left: 2%;
}

.hero-text-block h1 {
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.3;
  margin-left: 2%;
  margin-bottom: 2rem;
}

.hero-btn-block {
  margin-top: 2rem;
}

.hero-btn-block .btn {
  padding: 0.9rem 1rem;
  border-radius: 4px;
  box-shadow: none !important;
  cursor: pointer;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 9px;
  margin-left: 0;
  margin-right: 1%;
}

.hero-btn-block .btn-pink {
  color: white;
  margin-left: 2%;
  background-color: #fd7792;
  border: 1px solid #fd7792;
}
.hero-btn-block .btn-pink:hover {
  color: white;
  background: transparent;
  border: 1px solid #fd7792;
}

.hero-btn-block .btn-white {
  color: #000000;
  background-color: #fff;
  border: 1px solid #fff;
}

.hero-btn-block .btn-white:hover {
  border: 1px solid #fff;
  background: transparent;
  color: #fff;
}

.hero_book_form_block {
  display: none;
}

@media (min-width: 746px) {
  .hero_book_form_block {
    display: block;
  }
  .hero-btn-block .btn {
    font-size: 11px;
  }
}

@media (min-width: 1500px) {
  .hero_book_form_block {
    margin-right: 10%;
  }
}
