header {
  background-color: #e83e8c;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.875rem;
  padding: 0.5rem;
}

.contact-details {
  margin-left: 5%;
}

.mailus {
  color: rgba(255, 255, 255, 0.8);
}

.special-letter {
  color: white;
  text-decoration: none;
}

.social-icons {
  margin-right: 5%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.social-icons a {
  color: white;
  text-decoration: none;
  margin: 0 0.625rem;
}
